import React from 'react';
import { useStyles } from './styles';
import { TextExplore } from '../TextExplore';

interface IProps {
  Page: React.JSXElementConstructor<any>;
  title: string;
  isAppearing: boolean;
}

export const PageWrapper = React.memo((props: IProps) => {
  const { title, Page, isAppearing } = props;
  const classes = useStyles();
  return (
    <div className={classes.container}>
      <div className={isAppearing ? classes.fadeIn : classes.fadeOut} style={{ width: '100%' }}>
        <h6 style={{ margin: '0px 0px 0px 0px', fontFamily: 'sans-serif' }}>{title}</h6>
        <Page />
      </div>
    </div>
  );
});
