import React, { useEffect, useState } from 'react';
import { BrowserRouter as Router, Route, Switch, useHistory } from 'react-router-dom';
import { useParams, useLocation } from 'react-router';
import HomeIcon from '@material-ui/icons/Home';

import { useStyles } from './styles';
import { TextExplore } from '../TextExplore';
import { PageHome } from '../PageHome';
import { PageExplore } from '../PageExplore';
import { PageAbout } from '../PageAbout';
import { PageContact } from '../PageContact';
import { PageTravel } from '../PageTravel';
import { PageShop } from '../PageShop';
import { PageRead } from '../PageRead';
import { PageWrapper } from '../PageWrapper';
import { PageCinema } from '../PageCinema';

const links = [
  {
    label: () => <HomeIcon style={{ transform: 'translateY(5px)' }} />,
    link: '/'
  },
  {
    label: 'EXPLORE',
    link: '/explore'
  },
  {
    label: 'CINEMA',
    link: '/cinema'
  },
  {
    label: 'TRAVEL',
    link: '/travel'
  },
  {
    label: 'SHOP',
    link: '/shop'
  },
  {
    label: 'READ',
    link: '/read'
  }
];
export const layerHeight = 150;
export const animationDurationSecs = 0.5;
const collapseDelay = 1 + links.length * animationDurationSecs;

export const PetraLayout = () => {
  const classes = useStyles();
  const history = useHistory();
  const [isShrunk, setIsShrunk] = useState(false);
  const [isPageAppearing, setIsPageAppearing] = useState(false);
  const [isSiteLoaded, setIsSiteLoaded] = useState(false);
  const [pageIndex, setPageIndex] = useState(0);
  const { pathname } = useLocation();

  const pageChange = (newLink: string, newPageIndex: number) => {
    setPageIndex(newPageIndex);
    // setIsShrunk(newPageIndex !== 0);
    setIsShrunk(false);
    setIsPageAppearing(false);
    setTimeout(
      () => {
        history.push(newLink);
        setIsShrunk(newPageIndex !== 0);
        setIsPageAppearing(true);
      },
      pathname === '/' ? 10 : 1000
    );
  };

  useEffect(() => {
    setTimeout(() => {
      console.log('collapseDelay', collapseDelay);
      setIsShrunk(pathname !== '/');
      setIsSiteLoaded(true);
      setIsPageAppearing(true);
    }, collapseDelay * 1000);
  }, []);

  useEffect(() => {
    // setIsPageAppearing(false);
    setTimeout(() => {
      setIsShrunk(pathname !== '/');
      setIsPageAppearing(true);
    }, 500);
  }, [pathname]);

  return (
    <div className={classes.container} style={{ maxWidth: 800 }}>
      <div
        className={'templeWrapper'}
        style={{
          width: isShrunk ? '800px' : '400px',
          maxWidth: '100vw',
          marginLeft: 'auto',
          marginRight: 'auto',
          transition: 'width 0.5s ease-in-out'
        }}
      >
        <div
          className={classes.layer0Wrapper + ' ' + (isShrunk ? classes.shrink : classes.expand)}
          style={{ width: '100%' }}
        >
          <div
            className={classes.layer0}
            style={{
              width: '100%',
              height: layerHeight,
              backgroundImage: `url(images/${getLayer(0)}.png)`,
              // backgroundSize: 'contain',
              // backgroundSize: '50% auto',
              backgroundSize: '100% 100%',
              // backgroundRepeat: 'no-repeat',
              backgroundPosition: 'center'
            }}
          />
        </div>

        <div className={classes.layer1Wrapper} style={{ position: 'relative' }}>
          <div
            className={classes.layer1}
            style={{
              width: '100%',
              height: 100,
              backgroundImage: `url(images/${getLayer(1)}.png)`,
              // backgroundSize: 'contain',
              backgroundSize: '100% 100%',
              // backgroundSize: '50% auto',
              // backgroundSize: '50% auto',
              // backgroundRepeat: 'no-repeat',
              backgroundPosition: 'center',
              backgroundColor: 'transparent'
            }}
          />
          <div
            className={classes.laserTextWrapper}
            style={{
              position: 'absolute',
              top: 30,
              left: 8
            }}
          >
            {links.map((el, ind) => (
              <span
                key={ind}
                onClick={() => pageChange(el.link, ind)}
                style={{
                  marginLeft: 10,
                  border: pageIndex === ind ? '1px solid rgba(255,255,255,0.3)' : ''
                  // textShadow: pageIndex === ind ? '10px 10px 10px red' : ''
                }}
              >
                <TextExplore
                  text={el.label}
                  fontSize={14}
                  fontWeight={800}
                  fontColor={'white'}
                  // fontFamily="'PseudoSaudi'"
                  // fontFamily="'AlfredDrake'"
                  animationDelaySecs={1 + ind * 0.5}
                  animationDurationSecs={animationDurationSecs}
                />
              </span>
            ))}
          </div>
        </div>

        <div
          className={classes.layer2Wrapper + ' ' + (isShrunk ? classes.shrink : classes.expand)}
          style={{ width: '100%', maxWidth: 5000 }}
        >
          <div
            className={classes.layer2}
            style={{
              width: '100%',
              height: layerHeight,
              backgroundImage: `url(images/${getLayer(2)}.png)`,
              // backgroundSize: 'contain',
              // backgroundSize: '100% auto',
              backgroundSize: '100% 100%',
              backgroundRepeat: 'no-repeat',
              backgroundPosition: 'center'
            }}
          />
        </div>
      </div>

      <div
        className={classes.pageArena}
        style={{
          // height: 2500,
          width: '100%',
          // backgroundColor: '#F29D73',
          backgroundColor: 'white',
          boxShadow: '4px 0px 4px rgba(0,0,0,0.1), -4px 0px 4px rgba(0,0,0,0.1)',
          // backgroundColor: '#FFE0B0',
          // backgroundColor: '#F5C396', // yellow
          border: '5px solid #EE7547',
          boxSizing: 'border-box',
          padding: 20,
          // color: 'white',
          color: 'black',
          flex: 1,
          maxHeight: 'calc( 100vh - 100px - 50px)'
        }}
      >
        {isSiteLoaded && (
          <Switch>
            <Route exact path="/">
              <PageWrapper
                // title="WELCOME TO NABATAEA.NET"
                title="WELCOME"
                Page={PageHome}
                isAppearing={isPageAppearing}
              />
            </Route>
            <Route exact path="/explore">
              <PageWrapper title="EXPLORE" Page={PageExplore} isAppearing={isPageAppearing} />
            </Route>
            <Route path="/travel">
              <PageWrapper title="TRAVEL" Page={PageTravel} isAppearing={isPageAppearing} />
            </Route>
            <Route path="/cinema">
              <PageWrapper title="CINEMA" Page={PageCinema} isAppearing={isPageAppearing} />
            </Route>
            <Route path="/shop">
              <PageWrapper title="SHOP" Page={PageShop} isAppearing={isPageAppearing} />
            </Route>
            <Route path="/read">
              <PageWrapper title="READ" Page={PageRead} isAppearing={isPageAppearing} />
            </Route>
          </Switch>
        )}
      </div>

      <div className={classes.footer}>This is a Footer</div>
    </div>
  );
};

function getLayer(num: number) {
  return `layer${num}_v1`;
}
