import { makeStyles, Theme } from '@material-ui/core';

import { IProps } from './index';

export const useStyles = (props: IProps) => {
  const animationDurationSecs = props.animationDurationSecs || props.text.length / 2 || 4;
  const {
    width,
    height,
    animationDelaySecs,
    fontSize,
    laserThicknessParam,
    laserColor,
    fontColor,
    fontFamily,
    fontWeight,
    backgroundColor
  } = props;
  const laserThickness = (fontSize || 20) * (laserThicknessParam || 1);
  const lt = laserThickness;

  return makeStyles(
    (theme: Theme) => ({
      container: {
        width,
        height,
        backgroundColor,
        display: 'inline-block'
      },

      laserTextContainer: {
        display: 'flex'
      },

      title: {
        width,
        position: 'relative',
        textAlign: 'center',
        transformStyle: 'preserve-3d',
        whiteSpace: 'pre-wrap'
      },

      writeWrapper: {
        '&:hover': {
          // animationPlayState: 'paused'
          // backgroundColor: 'cyan'
        }
      },

      write: {
        width,
        overflow: 'visible',
        display: 'inline-block',
        color: fontColor,
        // textShadow: '0 0 0.1em currentColor',
        transformStyle: 'preserve-3d',
        animation: '$write linear both'
      },

      laserWrapper: {
        width,
        overflow: 'visible',
        position: 'absolute',
        top: 0,
        left: 0,
        bottom: 0,
        right: 0,
        backgroundColor: 'rgba(0,255,0,0.0)',
        color: laserColor,
        textShadow: '0 0 0.1em currentColor',
        transform: 'translateZ(5px)',
        filter: 'blur(4px) contrast(10)'
      },

      laser: {
        width,
        overflow: 'visible',
        position: 'absolute',
        top: 0,
        left: 0,
        pointerEvents: 'none',
        animation: '$laser linear both'
      },

      text: {
        width,
        overflow: 'visible',
        lineHeight: 1,
        fontFamily: fontFamily || 'sans-serif',
        fontWeight,
        fontSize,
        //
        animationDuration: `${animationDurationSecs}s`,
        animationDelay: `${animationDelaySecs}s`
        // animationIterationCount: 'infinite'
      },

      //
      '@keyframes write': {
        '0%': {
          clipPath: `polygon(-${lt}px -50%, 0% -50%, 0% 110%, -${lt}px 110%)`
        },
        '100%': {
          clipPath: `polygon(0% -50%, 100% -50%, 100% 110%, 0% 110%)`
        }
      },

      '@keyframes laser': {
        '0%': {
          clipPath: `polygon(-${lt}px 0%, 0% 0%, 0% 100%, -${lt}px 100%)`
        },
        '100%': {
          clipPath: `polygon( 100% 0%, 100% 0%, 100% 100%, 100%  100%)`
        }
      }
    }),
    { name: 'LT-' }
  );
};
