import React from 'react';

// import './laser-styles.scss';
import { useStyles } from './styles';

export interface IProps {
  text: string | React.JSXElementConstructor<any>;
  width?: string | number;
  height?: string | number;
  backgroundColor?: string;
  laserColor?: string;
  fontColor?: string;
  fontFamily?: string;
  fontWeight?: number;
  animationDurationSecs?: number;
  animationDelaySecs?: number;
  fontSize?: number;
  laserThicknessParam?: number; // multiply by fontSize to get thickness of laser; adjust for different fonts
}

const defaultProps: Partial<IProps> = {
  width: 'auto',
  height: 'auto',
  laserColor: 'white',
  fontColor: 'white',
  backgroundColor: 'transparent',
  animationDelaySecs: 0,
  fontSize: 80,
  laserThicknessParam: 0.2,
  fontWeight: 400
};

export const TextExplore = React.memo((props: IProps) => {
  const effectiveProps = { ...defaultProps, ...props };
  const classes = useStyles(effectiveProps)();
  const { text } = effectiveProps;
  const TextElement = typeof text === 'string' ? () => <>{text}</> : text;
  return (
    <div className={classes.container}>
      <div className={classes.laserTextContainer}>
        <div className={classes.title}>
          <div className={classes.writeWrapper}>
            <div className={classes.text + ' ' + classes.write}>
              <TextElement />
            </div>
          </div>
          <div className={classes.laserWrapper}>
            <div className={classes.text + ' ' + classes.laser} aria-hidden="true">
              <TextElement />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
});
